import React, { useEffect, useRef } from 'react';
import styled from 'styled-components';

const ProgressCircleWrap = styled.div`
  margin-top: 0.7rem;
`;

const ProgressCircleContainer = styled.div``;

const CircleChart = styled.div`
  position: relative;
  width: 62px;
  height: 62px;
  border-radius: 50%;
  transition: 0.3s;
`;

const CircleSubChart = styled.span`
  background: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  text-align: center;
  line-height: 40px;
  font-size: 16px;
  transform: translate(-50%, -50%); // 
  color: #07f;

  > span {
    font-weight: 700;
    font-size: 16px;
    font-family: Noto Sans KR;
  }
`;

function ProgressCircle({ color, percent }) {
  /* 
  ProgressCircle     2   . 
  
   <ProgressCircle color="#07f" percent={RankingData[0].win} />

   1 color:    
   2 percent:   
  */

  const chart1 = useRef(null); //   div 
  let i = 1;

  const colorFn = (i, color) => {
    // makeChart setInterval   conic-gradient  1% --> 100%  .
    chart1.current.style.background = `conic-gradient(${color} 0% ${i}%, #dedede 1% 100%)`;
  };

  const makeChart = (percent, color) => {
    const chartFunc = setInterval(() => {
      if (i <= percent) {
        if (chart1.current) {
          colorFn(i, color);
          i += 1;
        } else {
          return null; // cannot read property 'style'     else 
        }
      } else {
        clearInterval(chartFunc);
      }
    }, 10);
  };

  useEffect(() => {
    makeChart(percent, color);
  }, []); // useEffect   .

  return (
    <ProgressCircleWrap>
      <ProgressCircleContainer>
        <CircleChart ref={chart1}>
          <CircleSubChart>
            <span>{percent}%</span>
          </CircleSubChart>
        </CircleChart>
      </ProgressCircleContainer>
    </ProgressCircleWrap>
  );
}

export default ProgressCircle;
