import React from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import Search from './Search';

const NabCantainer = styled.div`
  height: 50%;
  width: 1000px;
  display: flex;
  align-items: center;
  justify-content: space-between;
`;

const TabBox = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  a {
    text-decoration: none;
  }
`;

const Tab = styled.div`
  position: relative;
  display: flex;
  align-items: center;
  box-sizing: border-box;
  padding: 0 2rem;
  height: 4rem;
  color: #ffffff;
  opacity: 0.5;

  :hover {
    color: #ffffff;
    cursor: pointer;
    opacity: 1;
    ::after {
      content: '';
      position: absolute;
      bottom: 0px;
      left: 0px;
      width: 100%;
      border-bottom: 4px solid #ffffff;
    }
  }
  ::after {
    content: '';
    position: absolute;
    bottom: 0px;
    left: 0px;
    width: 0%;
    border-bottom: 4px solid #ffffff;
    transition: 0.3s;
  }
`;

function NabBar() {
  return (
    <NabCantainer>
      <TabBox>
        <Link to="/">
          <Tab></Tab>
        </Link>
        <Link to="/rank">
          <Tab></Tab>
        </Link>
        <Tab></Tab>
        <Tab></Tab>
      </TabBox>
      <Search />
    </NabCantainer>
  );
}

export default NabBar;
