import React from 'react';
import styled from 'styled-components';

const RankItemTitle = () => (
  <ItemTitle>
    <RankNum>#</RankNum>
    <RankNickName>()</RankNickName>
    <RankPoint>()</RankPoint>
    <RankCount></RankCount>
    <RankAverge></RankAverge>
  </ItemTitle>
);

export default RankItemTitle;

const ItemTitle = styled.li`
  position: relative;
  line-height: 40px;
  height: 40px;
  padding-left: 60px;
  margin-bottom: 10px;
  background-color: transparent;
  border: none;
  color: #fff;
  font-size: 12px;
  font-weight: 500;
`;

const RankNum = styled.span`
  position: absolute;
  left: 60px;
  vertical-align: middle;
`;

const RankNickName = styled.span`
  position: absolute;
  left: 220px;
`;

const RankPoint = styled.span`
  position: absolute;
  right: 260px;
  display: inline-block;
  width: 120px;
`;

const RankCount = styled.span`
  position: absolute;
  right: 140px;
`;

const RankAverge = styled.span`
  position: absolute;
  right: 60px;
`;
